body {
  background: #3D3E43;
  margin: 0;
  padding: 0;
  font-family: 'Share Tech Mono', monospace; }

h2 {
  margin: 0;
  font-size: 30px;
  color: white; }

#root {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

#drum-machine {
  background: #393939;
  padding: 15px;
  width: 434px;
  min-width: 434px;
  box-sizing: border-box;
  border: 4px solid transparent;
  border-image: linear-gradient(to bottom, #00FF75 0%, #005527 100%);
  border-image-slice: 1; }

#title-container {
  height: 50px;
  margin-bottom: 10px; }

#panel {
  display: inline-block;
  width: 125px; }

#display-container {
  background: #f0fcfc;
  height: 28px;
  padding: 0 5px;
  border: 4px darkgreen solid;
  box-shadow: inset 2px 3px 4px #aaa; }

#display {
  font-size: 18px;
  color: #424242;
  line-height: 28px; }

#drum-pad {
  display: grid;
  float: right;
  width: 260px;
  height: 260px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 10px; }

.drum-pad {
  color: #444;
  font-size: 1.7em; }

.button-red {
  background: #ad3b1f;
  border: none;
  outline: none;
  user-select: none;
  border-radius: 5px; }

.button-red:hover {
  cursor: pointer; }

.button-red:active {
  background: radial-gradient(#ffc2b3 40%, #ff3300 130%);
  box-shadow: 0px 0px 35px -5px #ff3300; }

.button-red-active {
  background: radial-gradient(#ffc2b3 40%, #ff3300 130%);
  box-shadow: 0px 0px 35px -5px #ff3300; }

.button-green {
  background: #1fad42;
  border: none;
  outline: none;
  user-select: none;
  border-radius: 5px; }

.button-green:hover {
  cursor: pointer; }

.button-green:active {
  background: radial-gradient(#b3ffc6 40%, #00ff40 130%);
  box-shadow: 0px 0px 35px -5px #00ff40; }

.button-green-active {
  background: radial-gradient(#b3ffc6 40%, #00ff40 130%);
  box-shadow: 0px 0px 35px -5px #00ff40; }

