  border-radius: 5px; }

.button-blue:hover {
  cursor: pointer; }

.button-blue:active {
  background: radial-gradient(#b3dfff 40%, #0095ff 130%);
  box-shadow: 0px 0px 35px -5px #0095ff; }

.button-blue-active {
  background: radial-gradient(#b3dfff 40%, #0095ff 130%);
