.button-purple-active {
  background: radial-gradient(#ecb3ff 40%, #bf00ff 130%);
  box-shadow: 0px 0px 35px -5px #bf00ff; }

#bank-pad {
  display: block; }

[id^="bank-pad-"] {
  width: calc(100% - 4px);
  margin: 0 2px;
  margin-top: 15px;
  padding: 0;
  height: 45px;
  font-size: 22px; }

.bank-pad-button-not-selected {
  background: #4d4d4d;
  border: none;
  outline: none;
  user-select: none;
  border-radius: 5px; }

.bank-pad-button-not-selected:hover {
  cursor: pointer; }

#bank-pad > button {
  margin-top: 16px; }

#slider-container {
  margin: 0;
  padding: 5px;
  padding-top: 15px; }

.slider {
  margin: 0;
  width: 100%;
  -webkit-appearance: none;
  height: 3px;
  border-radius: 5px;
  background: #999;
  outline: none; }

input[type=range]::-moz-range-track {
  background: none; }

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #1fad42;
  cursor: pointer; }

.slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #1fad42;
  cursor: pointer; }
